var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("服務項目")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("美膚光療")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("生化雷射")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"生化雷射","titleEn":"ILIB"}}),_vm._ssrNode(" <div class=\"article-container service-page\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ilib/banner.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ilib/banner.jpg")))+" alt=\"生化雷射 Banner\"></picture> <p>現代人生活在空氣汙染，飲水汙染和輻射的環境裡，加上不良的生活習慣、情緒緊張、環境壓力，導致血液中滿了毒素。利用低能量的雷射光導入血液中經由全身循環、活化生理功能、清除體中毒素，達到排毒修護、預防疾病和改善慢性病的效果。</p></div> <div class=\"article-container\"><h2>生化雷射的作用原理</h2> <h3>雷射低能量生化療法是一種換能療法</h3> <p>生化雷射又稱氦氖雷射、低能量生化雷射、靜脈雷射或ILIB(Intravenous　Laser Irradiation of Blood)，氦氖雷射是屬於物理性的一種，是用632.8波長6mW以下的低能量紅色生化雷射光，產生光化學效應，活化血球對於血液中循環的血球與各種細胞有改善功能、促進代謝、清除自由基。</p> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ilib/1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ilib/1.jpg")))+" alt=\"生化雷射的作用原理\"></picture> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ilib/2.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ilib/2.jpg")))+" alt=\"紅血球的反應\"></picture></div> <div class=\"article-container\"><h2>生化雷射的分析</h2> <p>利用光纖將低能量的雷射導入血液後，光能會轉化成生物能量，使紅血球的攜氧能力增加，新陳代謝會增強，白血球及其他免疫相關的機能也會改善。不論是做為輔助治療、正規醫療或是保健抗衰老的預防醫療來說，都有很好的效果。</p> <p>治療後睡眠會立刻改善，其它效果也會慢慢出現。低強度生化雷射的治療效果是溫和漸進的，如果治療後反應不明顯表示您的症狀比較嚴重，繼續治療效果就會慢慢出現。</p></div> <div class=\"article-container\"><h2>生化雷射的治療方式</h2> <ul><li>+ 以低強度(低能量)生化雷射經由靜脈照射血液，每次60分鐘，連續十次為一個療程。</li> <li>+ 生化雷射不使用任何藥物，治療時沒有不舒服的感覺，治療完成後可以立刻上班上課。</li> <li>+ 一個療程後就會見到效果，症狀嚴重的患者照射三個-五個療程效果最佳，每個療程間隔時間為7天-10天。</li></ul></div> <div class=\"article-container\"><h2>生化雷射 光的生物效應</h2> <h3>人體的光合作用機</h3> <ul><li>+ <b>減少慢性病用藥</b>高血壓、高血脂、糖尿病、高膽固醇痛風</li> <li>+ <b>促進代謝排毒</b>代謝症候群、尿毒症、戒毒、解酒</li> <li>+ <b>活化機能 修復受損細胞</b> 毛髮增生、細緻皮膚、減少感染、加速傷口癒合、加速產後復元、苗條身材體態</li> <li>+ <b>調節免疫力 抵抗發炎</b> 急慢性肝炎、腸胃道慢性發炎、癌症輔助治療、氣喘、慢性濕疹、異位性皮膚炎、細菌感染、過敏性鼻炎、紅斑性郎瘡、慢性疲勞症候群、乾癬、疼痛舒緩</li> <li>+ <b>促進血液循環</b> 貧血、血管硬化、頭痛、耳鳴、眩暈、心臟病、神經衰弱、手腳麻痺、中風、腦血管病變</li> <li>+ <b>慢性病復健</b> 退化性關節炎、類風溼性關節炎、狹心症、坐骨神經痛、五十肩、停經症候群、神經麻痛、梗塞性腦中風、睡眠障礙、面神經麻痺</li></ul></div> <div class=\"article-container\"><h2>適用對象</h2> <ul><li>+ 欲提高新陳代謝使廢物不易囤積、更年期障礙。</li> <li>+ 高血脂、高尿酸(痛風)和糖尿病等慢性病患者，希望能減少服藥、減輕症狀者。</li> <li>+ 睡眠困擾、自律神經失調、情緒障礙又不希望長期服藥的人。</li> <li>+ 免疫力不好，常感冒或感冒不容易好的人。</li> <li>+ 預防心肌梗塞、中風和暈眩、排除體內毒素、調節免疫力、提高抗病力者。</li></ul></div> <div class=\"article-container\"><h2>貼心小提醒</h2> <ul><li>+ 治療後為使新陳代謝旺盛，注意血糖的維持，故不宜空腹。</li> <li>+ 治療後為使血液循環量充足，故多補充水分，請喝約500-1000CC溫開水，以免影響療效。</li> <li>+ 飲食均衡勿過量，多吃綠葉蔬菜和水果。經常散步運動，早睡早起勿熬夜。如果有任何不適請與診所聯絡。</li></ul></div> <div class=\"article-container\"><h2>《中天新聞 醫療最前線》生化雷射(靜脈雷射)</h2> <iframe src=\"https://www.youtube.com/embed/v4VOsg3CmUg\" title=\"YouTube video player\" frameborder=\"0\" allow=\"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen=\"allowfullscreen\"></iframe></div>")],2)}
